 
 $theme: (
    "primary": #121221,
    "secondary": #FFFFFF
); 
 @import '~include-media/dist/_include-media.scss';

@mixin hvr-bounce-to-bottom($colorPrimary: map-get($theme, "primary"), $colorSecondary: map-get($theme, "secondary")) {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.53;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $colorPrimary;
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;
    }
    &:hover, 
    &:focus,
    &:active {
        color: $colorSecondary;
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        }
    }
}


@mixin hvr-underline-from-center {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: map-get($theme, "secondary");
        height: 4px;
        transition-property: left, right;
        transition-duration: 0.3s;
        transition-timing-function: ease-out;
    }
    &:hover,
    &:focus,
    &:active {
        &:before {
            left: 12%;
            right: 12%;
        }
    }
}

@mixin hvr-slide-from-up {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.5s;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: map-get($theme, "secondary");
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;  
    }
    &:hover,
    &:active,
    &:focus {
        color: map-get($theme, "primary");
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); 
        }
    }
} 
 /* this import includes relaway & cabin font; I'm using a single import to minimize the requests */
@import url('https://fonts.googleapis.com/css2?family=Arvo&family=Cabin&family=Raleway&display=swap');
$fonts: (
    "primary": 'Raleway', 
    "secondary": 'Cabin',
    "unique": 'Arvo'
);

.portfolio {
    &__wave {
        height: 200px;
        position: relative;
        overflow: hidden;
        transform: translateY(-10px);
        > div {
            transform: translateY(20px) scale(1.1);
            width: 110vw;
            position: absolute;
            height: auto !important;
            top: -25vw;
            z-index: 1;
        }
    }
    &__wrapper {
        justify-content: center;
        height: 1160vh;
        color: map-get($theme, "secondary");
        background: map-get($theme, "primary");
        text-transform: uppercase;
        margin: 0;
        margin-top: -2rem;
        position: relative;
        overflow: hidden;
        max-width: 100vw;
        &--no-overflow {
            overflow: unset;
        }
        span {
            display: block;
        }
    }
    &__title {
        text-align: center;
        display: flex;
        justify-content: center;
        span {
            background: map-get($theme, "primary");
            padding: 1rem;
        }
        &__wrapper {
            position: sticky;
            top: 0;
            z-index: 2;
            &--active {
                opacity: 1 !important;
                transform: scale(0.5) !important;
            }
        }     
    }
    &__item {
        display: flex;
        padding: 0 4rem;
        height: 100vh;
        position: sticky;
    }
    &__sep {
        height: 25vh;
    }
    &__screenshots {
        width: 70%;
        position: relative;
        &--desktop, &--mobile {
            width: 70%;
            height: 80%;
            position: absolute;
            bottom: 0;
            overflow: hidden;
            img {
                max-width: 100%;
            }
        }
        &--mobile {
            width: auto;
            height: 80%;
            width: 25%;
            right: 8rem;
            box-shadow: -6px 10px 14px -2px rgba(0,0,0,0.48);
        }
    }

    &__description {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 30%;
    }
}