 
 $theme: (
    "primary": #121221,
    "secondary": #FFFFFF
); 
 @import '~include-media/dist/_include-media.scss';

@mixin hvr-bounce-to-bottom($colorPrimary: map-get($theme, "primary"), $colorSecondary: map-get($theme, "secondary")) {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.53;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $colorPrimary;
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;
    }
    &:hover, 
    &:focus,
    &:active {
        color: $colorSecondary;
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        }
    }
}


@mixin hvr-underline-from-center {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: map-get($theme, "secondary");
        height: 4px;
        transition-property: left, right;
        transition-duration: 0.3s;
        transition-timing-function: ease-out;
    }
    &:hover,
    &:focus,
    &:active {
        &:before {
            left: 12%;
            right: 12%;
        }
    }
}

@mixin hvr-slide-from-up {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.5s;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: map-get($theme, "secondary");
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;  
    }
    &:hover,
    &:active,
    &:focus {
        color: map-get($theme, "primary");
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); 
        }
    }
} 
 /* this import includes relaway & cabin font; I'm using a single import to minimize the requests */
@import url('https://fonts.googleapis.com/css2?family=Arvo&family=Cabin&family=Raleway&display=swap');
$fonts: (
    "primary": 'Raleway', 
    "secondary": 'Cabin',
    "unique": 'Arvo'
);

.header {
        display: flex;
        height: 3.75rem;
        justify-content: flex-end;
        align-items: center;
        padding: 0 1rem;
        &__nav {
            position: fixed;
            top: 0;
            left: 1rem;
            transition: all 0.2s;
            z-index: 10;
            @include media("<=tablet") {
                left: 50vw;
                transform: translateX(-50%);
            }
            &:hover {
                .header__logo {
                    transform: scale(1.01);
                    background-color: map-get($theme, "secondary");
                    color: map-get($theme, "primary");
                    border: solid 1px map-get($theme, "primary");
                    border-radius: 0;
                    div {
                        opacity: 0;
                    }
                }
                top: 0.625rem;
            }
        }

    &__logo {
        transform: translateX(-5px);
        border: solid 1px map-get($theme, "secondary");
        border-top: solid 1px map-get($theme, "primary");
        background-size: contain;
        background-repeat: no-repeat;
        width: 10.7rem;
        border-radius: 0 0 2rem 2rem;
        font-family: map-get($fonts, "primary");
        padding: 1rem;
        color: map-get($theme, "secondary");
        background-color: map-get($theme, "primary");
        user-select: none;
        transition: all 0.3s;
        position: relative;
        z-index: 50;
        cursor: pointer;
        text-align: center;
    }

    &__contact {
        font-family: map-get($fonts, "secondary");
        text-decoration: none;
        border: solid 3px map-get($theme, "primary");
        padding: 0.625rem 1rem;
        border-radius: 0.3rem;
        cursor: pointer;
        color: map-get($theme, "primary");
        @include hvr-bounce-to-bottom;
        @include media("<=tablet") {
            display: none;
        }
    }
}

.nav {
    &-menu {
        width: 100%;
        display: flex;
        justify-content: center;
        &-list {
            .header__nav:hover & {
                height: 21.75rem;
                padding-top: 1.25rem;
                border: 2px map-get($theme, "primary") solid;
            }
            &__item {
                width: 100%;
                box-sizing: border-box;
                padding: 1.25rem 0;
                display: flex !important;
                align-items: center;
                justify-content: center;
                color: map-get($theme, "secondary");
                font-family: map-get($fonts, "primary");
                text-decoration: none;
                transition: all 0.3s ease-out;
                @include hvr-underline-from-center;
                &:last-child {
                    font-size: 1.5rem;
                    &:hover {
                        transform: scale(1.2);
                        &:before {
                            display: none;
                        } 
                    }
                }
            }
            

            transform: translateY(-0.625rem);
            width: 10rem;
            height: 0;
            overflow: hidden;
            background-color: map-get($theme, "primary");
            transition: all 0.4s;
            transition-delay: 0.2s;
            transition-timing-function: ease-out;
            position: relative;
            z-index: 1;
            box-sizing: border-box;
            clip-path: polygon(0 0, 100% 0, 100% 90%, 50% 100%, 0 90%);
            border: 2px transparent solid;
        }
    }
}