 
 $theme: (
    "primary": #121221,
    "secondary": #FFFFFF
); 
 @import '~include-media/dist/_include-media.scss';

@mixin hvr-bounce-to-bottom($colorPrimary: map-get($theme, "primary"), $colorSecondary: map-get($theme, "secondary")) {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.53;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $colorPrimary;
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;
    }
    &:hover, 
    &:focus,
    &:active {
        color: $colorSecondary;
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        }
    }
}


@mixin hvr-underline-from-center {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: map-get($theme, "secondary");
        height: 4px;
        transition-property: left, right;
        transition-duration: 0.3s;
        transition-timing-function: ease-out;
    }
    &:hover,
    &:focus,
    &:active {
        &:before {
            left: 12%;
            right: 12%;
        }
    }
}

@mixin hvr-slide-from-up {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.5s;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: map-get($theme, "secondary");
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;  
    }
    &:hover,
    &:active,
    &:focus {
        color: map-get($theme, "primary");
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); 
        }
    }
} 
 /* this import includes relaway & cabin font; I'm using a single import to minimize the requests */
@import url('https://fonts.googleapis.com/css2?family=Arvo&family=Cabin&family=Raleway&display=swap');
$fonts: (
    "primary": 'Raleway', 
    "secondary": 'Cabin',
    "unique": 'Arvo'
);

.skills {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    &__wrapper {
        height: 600vh;
    }
    &__circle {
        height: 75vmin;
        width: 75vmin;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }

    &__border {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        border-radius: 50%;
        border: solid 1px map-get($theme, "primary");
        transition: opacity 0.2s ease-in-out;
        .skills--out & {
            opacity: 0;
        }
    }

    &__title {
        height: 80%;
        width: 80%;
        border-radius: 50%;
        background: map-get($theme, "primary");
        color: map-get($theme, "secondary");
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-family: map-get($fonts, "secondary");
        flex-direction: column;
        font-size: 1.5rem;
        text-align: center;
        transition: transform 0.2s ease-in-out;
        .skills--out & {
            transform: scale(4);
        }
        h1, p {
            transition: opacity 0.15s ease-in-out;
            .skills--out & {
                opacity: 0;
            }
        }

        h1 {
            margin-bottom: 0.5rem;
        }
        p {
            padding: 0 1.5rem;
            font-size: 1rem;
            font-weight: lighter;
            text-transform: none;
        }
    }

    &__skill {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        height: 30px;
        width: 30px;
        border-radius: 50%;
        background: map-get($theme, "primary");
        transition: all 0.2s ease-out;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            opacity: 0;
            transition: opacity 0.2s ease-out;
        }
        span {
            background-color: map-get($theme, "primary");
            color: map-get($theme, "secondary");
            padding: 0.3rem 1rem 0.3rem 1.5rem;
            font-size: 0.8rem;
            font-weight: lighter;
            text-transform: uppercase;
            transition: all 0.2s ease-out;
            transform: translateX(50%) scale(0);
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            display: flex;
            align-items: center;
            justify-content: center;
            clip-path: polygon(10% 0, 100% 0, 100% 100%, 10% 100%, 0 50%);
        }
        &--opened {
            height: 70px;
            width: 70px;
            img {
                opacity: 0.85;
                &:hover {
                    opacity: 1;
                }
            }
            &:hover {
                span {
                    transform: translateX(105%) scale(1);
                }
            }
        }
    }


}