 
 $theme: (
    "primary": #121221,
    "secondary": #FFFFFF
); 
 @import '~include-media/dist/_include-media.scss';

@mixin hvr-bounce-to-bottom($colorPrimary: map-get($theme, "primary"), $colorSecondary: map-get($theme, "secondary")) {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.53;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $colorPrimary;
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;
    }
    &:hover, 
    &:focus,
    &:active {
        color: $colorSecondary;
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        }
    }
}


@mixin hvr-underline-from-center {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: map-get($theme, "secondary");
        height: 4px;
        transition-property: left, right;
        transition-duration: 0.3s;
        transition-timing-function: ease-out;
    }
    &:hover,
    &:focus,
    &:active {
        &:before {
            left: 12%;
            right: 12%;
        }
    }
}

@mixin hvr-slide-from-up {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.5s;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: map-get($theme, "secondary");
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;  
    }
    &:hover,
    &:active,
    &:focus {
        color: map-get($theme, "primary");
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); 
        }
    }
} 
 /* this import includes relaway & cabin font; I'm using a single import to minimize the requests */
@import url('https://fonts.googleapis.com/css2?family=Arvo&family=Cabin&family=Raleway&display=swap');
$fonts: (
    "primary": 'Raleway', 
    "secondary": 'Cabin',
    "unique": 'Arvo'
);

.get-in-touch {
    text-align: center;
    margin-top: 15rem;
    &__animation-wrapper {
        height: 300px;
        width: 500px;
    }
    &__desc {
        font-weight: 300;
        width: 800px;
        max-width: 100%;
        padding:0 2rem;
        margin: 5rem auto 2rem;
    }

    &__actions {
        width: 800px;
        max-width: 100%;
        padding:0 2rem;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        gap: 1rem;
        .btn {
            font-family: map-get($fonts, "secondary");
            text-decoration: none;
            border: solid 3px map-get($theme, "primary");
            padding: 0.625rem 1rem;
            border-radius: 0.3rem;
            cursor: pointer;
            color: map-get($theme, "primary");
            @include hvr-bounce-to-bottom;
        }
        .btn__calender {
            color: map-get($theme, "secondary");
            background-color: map-get($theme, "primary");
            @include hvr-bounce-to-bottom(map-get($theme, "secondary"), map-get($theme, "primary"));
        }
    }
    
}