 
 $theme: (
    "primary": #121221,
    "secondary": #FFFFFF
); 
 @import '~include-media/dist/_include-media.scss';

@mixin hvr-bounce-to-bottom($colorPrimary: map-get($theme, "primary"), $colorSecondary: map-get($theme, "secondary")) {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.53;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: $colorPrimary;
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;
    }
    &:hover, 
    &:focus,
    &:active {
        color: $colorSecondary;
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66);
        }
    }
}


@mixin hvr-underline-from-center {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        left: 51%;
        right: 51%;
        bottom: 0;
        background: map-get($theme, "secondary");
        height: 4px;
        transition-property: left, right;
        transition-duration: 0.3s;
        transition-timing-function: ease-out;
    }
    &:hover,
    &:focus,
    &:active {
        &:before {
            left: 12%;
            right: 12%;
        }
    }
}

@mixin hvr-slide-from-up {
    display: inline-block;
    vertical-align: middle;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    position: relative;
    transition-property: color;
    transition-duration: 0.5s;
    &:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: map-get($theme, "secondary");
        transform: scaleY(0);
        transform-origin: 50% 0;
        transition-property: transform;
        transition-duration: 0.5s;
        transition-timing-function: ease-out;  
    }
    &:hover,
    &:active,
    &:focus {
        color: map-get($theme, "primary");
        &:before {
            transform: scaleY(1);
            transition-timing-function: cubic-bezier(0.52, 1.64, 0.37, 0.66); 
        }
    }
} 
 /* this import includes relaway & cabin font; I'm using a single import to minimize the requests */
@import url('https://fonts.googleapis.com/css2?family=Arvo&family=Cabin&family=Raleway&display=swap');
$fonts: (
    "primary": 'Raleway', 
    "secondary": 'Cabin',
    "unique": 'Arvo'
);

.open-source {
    margin-top: 10rem;
    max-width: 100vw;
    position: sticky;
    top: 10rem;
    &__wrapper {
        height: 400vh;
    }
    &__title {
        text-align: center;
    }
    &__desc {
        text-align: center;
        font-weight: 300;
        width: 800px;
        max-width: 100%;
        padding:0 2rem;
        margin: 0 auto;
        margin-bottom: 2rem;
    }
    &__projects {
        max-width: 100%;
        overflow: scroll;
        padding: 0 2rem;
        -ms-overflow-style: none;  /* IE and Edge */
        scrollbar-width: none;  /* Firefox */
        &::-webkit-scrollbar {
            display: none;
                  }
        > div {
            display: flex;
        }
    }
    &__card {
        width: 400px;
        height: 170px;
        min-width: 400px;
        display: inline-flex;
        flex-direction: column;
        padding: 1rem 2rem;
        background: map-get($theme, "secondary");
        box-shadow: 0px 2px 8px 1px rgba(0,0,0,0.1);
        border-radius: 2px;
        margin: 1rem;
        text-decoration: none;
        color: map-get($theme, "primary");
        transition: all 0.2s ease-in;

        &__title {
            margin-bottom: 0;
        }
        &__desc {
            font-weight: 300;
            margin-bottom: 1rem;
            opacity: 0.8;
            flex-grow: 1;
        }
        &__links {
            display: flex;
            justify-content: space-between;
        }
        &__contributes {
            img {
                border-radius: 50%;
                width: 35px;
                height: 35px;
            }
        }
        &__metrics {
            display: flex;
            align-items: center;
        }
        &__metric {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin-left:1rem;
        }
    }
}